.nav-list li {
    padding-top: 5px;
    padding-bottom: 5px;
}
 .nav-list li a:hover:before {
    margin-left: 0;
    opacity: 1;
    visibility: visible;
    text-decoration: none;
}
 ul, ol {
    list-style: none;
    padding: 0;
    margin: 0;
}
 .social-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 23px;
    font: 900 13px/1 "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    
}


.social-container .col {
    border: 2px solid rgba(221, 221, 221, 0.1);
}

.social-container a:link, a:visited {
    color: rgb(190, 185, 185);
}

.social-container a:hover, a:active {
    color: white;
    text-decoration: none;
}

.nav-list li a:before {
    content: "\F10E";
    font: 400 21px/1 "Material Design Icons";
    color: #f5c280;
    display: inline-block;
    vertical-align: baseline;
    margin-left: -29px;
    margin-right: 12px;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: .22s ease;
    transition: .22s ease;
} 

.nav-list li a:link, a:visited {
    color: rgb(190, 185, 185);
}

.nav-list li a:hover, a:active {
    color: white;
    text-decoration: none;
}

